  box-shadow: 0-1px 0 rgba(30, 32, 37, 0.08) inset;
  border-radius: var(--br-8xs);
  background-color: var(--white-color);
  padding: var(--padding-smi) var(--padding-8xs) var(--padding-mini)
    var(--padding-4xs);
  gap: var(--gap-lgi);
  cursor: pointer;
  z-index: 2;
}
.third-star-rating {
  align-self: stretch;
  padding: 0 var(--padding-12xs) 0 var(--padding-11xs);
}
.third-rating-container {
  flex: 1;
  box-shadow: 0-1px 0 rgba(30, 32, 37, 0.08) inset;
  border-radius: var(--br-8xs);
  background-color: var(--white-color);
  flex-direction: row;
  padding: var(--padding-smi) var(--padding-8xs) var(--padding-mini)
    var(--padding-4xs);
  gap: var(--gap-lgi);
  z-index: 1;
}
.fourth-star-rating,
.third-rating-container,
.third-star-rating1,
.top8 {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.third-star-rating1 {
  align-self: stretch;
  flex-direction: row;
  padding: 0 var(--padding-11xs) 0 var(--padding-12xs);
}
.fourth-star-rating,
.top8 {
  background-color: var(--white-color);
}
.fourth-star-rating {
  align-self: stretch;
  box-shadow: 0-1px 0 rgba(30, 32, 37, 0.08) inset;
  border-radius: var(--br-8xs);
  flex-direction: row;
  padding: var(--padding-smi) var(--padding-8xs) var(--padding-mini)
    var(--padding-4xs);
  gap: var(--gap-lgi);
  z-index: 1;
}
.top8 {
  position: absolute;
  top: 230px;
  left: 261px;
  border-radius: var(--br-3xs);
  width: 268px;
  flex-direction: column;
  padding: var(--padding-mini) var(--padding-9xs) 271px;
  box-sizing: border-box;
  gap: 13px;
  font-size: var(--font-size-base);
  color: var(--natural-color);
}
.candidates1 {
  width: 100%;
  height: 900px;
  position: relative;
  background-color: var(--color-ghostwhite);
  overflow: hidden;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  font-size: var(--font-size-sm);
  color: var(--natural-color-3);
  font-family: var(--font-open-sans);
}
@media screen and (max-width: 450px) {
  .redacted2 {
    font-size: var(--font-size-base);
  }
  .candidates2 {
    font-size: var(--font-size-lgi);
  }
  .total-candidates5,
  .total-candidates6 {
    font-size: var(--font-size-base);
  }
}
